#data-container {
  height: 100%;
}
#data {
  height: 100%;
  border-radius: 0.3rem;
  position: relative;
  border: 1px solid #d3d6da;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
#judge {
  position: absolute;
  top: -100%;
  width: 30%;
  left: 34%;
}
#result-raise,
#result-call,
#result-fold,
#result-no-answer {
  text-align: center;
  padding: 100px 10px;
  padding: 100px 10px;
  font-size: 1.5em;
  color: white;
}
.icon-circle {
  top: -60px;
  left: -55px;
  position: absolute;
  color: red;
  font-size: 250px;
}
.icon-xmark {
  top: -90px;
  left: -25px;
  position: absolute;
  color: blue;
  font-size: 300px;
}
