#game {
  width: 100%;
  max-width: var(--game-max-width);
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-direction: column;
}
#game-container,
#board-container,
#action-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
}
#hand {
  width: 450px;
  height: auto;
  padding: 10px;
  box-sizing: border-box;
}
#board {
  width: 450px;
  height: 250px;
  padding: 10px;
  box-sizing: border-box;
  position: relative;
}
#pod-sum {
  color: aliceblue;
  position: absolute;
  top: 10%;
  right: 5%;
  font-size: xx-large;
  font-weight: 900;
}
#actions {
  width: 450px;
  height: auto;
  box-sizing: border-box;
}
#action-button-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
