#player-button-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
#player-button-container .position {
  text-align: center;
  padding: 10px 0;
  width: 35%;
  margin: 10px auto 20px;
  color: aliceblue;
  background-color: darkgrey;
  border: 1px solid transparent;
  border-color: aliceblue;
  font-size: 2rem;
  line-height: 1.5;
  border-radius: 0.25rem;
}
#player-button-container .back-button,
#player-button-container .next-button {
  text-align: center;
  width: 30%;
  margin: 20px auto;
  color: aliceblue;
  background-color: #282c34;
  border: 1px solid transparent;
  border-color: aliceblue;
  font-size: 2rem;
  line-height: 1.5;
  border-radius: 0.25rem;
}
#card-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
