.actions {
  display: flex;
}
.raise-button,
.call-button,
.fold-button {
  text-align: center;
  width: 30%;
  margin: auto;
  color: aliceblue;
  border: 2px solid transparent;
  border-color: aliceblue;
  font-size: 2rem;
  font-weight: 700;
  line-height: 1.5;
  border-radius: 0.25rem;
}
.raise-button {
  background-color: rgb(231, 105, 119);
}
.call-button {
  background-color: rgb(159, 217, 113);
}
.fold-button {
  background-color: rgb(91, 177, 240);
}
